.header {
    position: fixed;
    transition: .1s;
    left: 0;
    right: 0;
    top: 0;
    padding: 20px;
    display: flex;
    background: white;
    box-shadow: 0px 5px 20px 0px rgba(0, 0, 0, 0.2);

    &__theme-toggle {
        font-size: 25px;
        cursor: pointer;
        display: grid;

        div {
            grid-row: 1/2;
            grid-column: 1/2;
        }
    }

    &__night {
        opacity: 0;
    }

    &__light {
        opacity: 1;
    }

    &__body {
        max-width: 850px;
        width: 98%;
        margin: 0 auto;
    }
}

.dark {
    .header {
        background: #0a0a0a;
    }

    .header__night {
        opacity: 1 !important;
    }

    .header__light {
        opacity: 0 !important;
    }
}