$primary: #ff7000;
$accent: rgb(4, 190, 76);
$warn: rgb(255, 0, 0);

body {
    font-family: 'Ubuntu', sans-serif;
    
    color: black;

    .text-p {
        color: $primary;
    }
    
    .text-a {
        color: $accent;
    }

    .text-w {
        color: $warn;
    }

    .text-small {
        font-size: .8em;
        color: grey;
    }

    a {
        text-decoration: none;
        color: black;
    }
}

.dark {
    color: white;

    background: black;

    a {
        color: white;
    }
}